import React from 'react';
import { Card, CardContent, Typography, Grid} from '@material-ui/core';
import styles from './Cards.module.css';
import CountUp from 'react-countup';
import cx from 'classnames';

const Cards =({ data: {confirmed, recovered, deaths, lastUpdate}}) => {
    //console.log(props);
    if(!confirmed){
        return 'Loading...'
    }
    return(
        <div className = {styles.container}>
            <Grid container spacing={3} justify="center">
                <Grid item component={Card} xs={12} md={3} className = {cx(styles.card, styles.infected)}>
                    <CardContent>
                        <Typography color="textPrimary" gutterBottom>Infected</Typography>
                        <Typography variant="h5">
                            <CountUp start = {0} end={confirmed.value} duration = {1.5}  separator = "," />
                        </Typography>
                        <Typography color="textSecondary">{new Date(lastUpdate).toDateString()}</Typography>
                        <Typography variant="body2"> Number of active cases of Covid-19</Typography>
                        
                    </CardContent>
                </Grid>

                <Grid item component={Card} xs={12} md={3} className = {cx(styles.card, styles.recovered)}>
                    <CardContent>
                        <Typography color="textPrimary" gutterBottom>Recovered</Typography>
                        <Typography variant="h5">
                            <CountUp start = {0} end={recovered.value} duration = {1.5}  separator = "," />
                        </Typography>
                        <Typography color="textSecondary">{new Date(lastUpdate).toDateString()}</Typography>
                        <Typography variant="body2"> Number of Recoveries from Covid-19</Typography>
                        
                    </CardContent>
                </Grid>

                <Grid item component={Card} xs={12} md={3} className = {cx(styles.card, styles.deaths)}>
                    <CardContent>
                        <Typography color="textPrimary" gutterBottom>Deaths</Typography>
                        <Typography variant="h5">
                            <CountUp start = {0} end={deaths.value} duration = {1.5}  separator = "," />
                        </Typography>
                        <Typography color="textSecondary">{new Date(lastUpdate).toDateString()}</Typography>
                        <Typography variant="body2"> Number of Deaths caused by Covid-19</Typography>
                        
                    </CardContent>
                </Grid>

            </Grid>

        </div>
    )
    
}

export default Cards;